@font-face {
  font-weight: 300;
  font-family: "open_sans";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff"); }

@font-face {
  font-weight: 700;
  font-family: "open_sans";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff"); }

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  min-width: 320px;
  font-weight: 300;
  font-size: 15px;
  line-height: 24px;
  font-family: "open_sans", "Arial", sans-serif;
  color: #444444;
  letter-spacing: normal;
  background-color: white; }

*,
*::before,
*::after {
  box-sizing: inherit; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0, 0, 0, 0); }

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  width: 100%;
  padding: 22px; }

@media (min-width: 768px) {
  .container {
    margin: 0 auto;
    width: 768px;
    background-color: red; } }

@media (min-width: 1150px) {
  .container {
    width: 1150px; } }

.button {
  border: none;
  background: none;
  text-align: center; }
  .button--transparent {
    width: 100%;
    display: block;
    border: solid 3px #ffffff;
    padding: 20px 40px 17px 40px;
    font-size: 18px;
    font-weight: 700;
    line-height: 18px;
    border-radius: 40px; }

.page-header {
  background-color: #283645;
  box-shadow: 0 1px 0 0 black;
  color: white;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 18px;
  line-height: 30px; }

.page-header__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 1px 0 0 black;
  margin-bottom: 1px;
  opacity: 0.3; }

.page-header__button {
  width: 50px;
  height: 24px; }

.page-header__button > svg {
  height: 100%; }

.page-header__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: none; }

.page-header__item {
  box-shadow: 0 1px 0 0 black, inset 0 1px 0 0 rgba(255, 255, 255, 0.15);
  margin-bottom: 1px; }

.page-header__item a {
  display: block;
  text-align: center;
  width: 100%;
  height: 100%;
  padding: 26px; }

.intro {
  margin-top: -77px;
  background-color: #283645;
  color: #ffffff;
  background-image: url("/img/bg-back-mobile@1x.jpg");
  background-size: 100% auto; }

.intro__wrapper {
  padding: 22px;
  width: 100%;
  background-color: #283645; }
  .intro__wrapper--main {
    margin-top: 462px; }

.intro__title {
  display: none; }

.intro__apps {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  align-items: baseline;
  justify-content: space-between;
  padding: 30px 0;
  max-width: 205px;
  margin: 0 auto; }

.intro__link {
  display: inline-flex; }

.intro__app {
  display: inline-flex; }

.intro__note {
  font-size: 14px;
  font-weight: 300;
  line-height: 24px;
  text-align: center; }
